import React, { useMemo } from 'react'
import { useFormik } from 'formik'

// import imgSize from '../TrashPage/data/imgSize';
import useData from '../TrashPage/data/useData'

import Box from '../../components/Box';
import Link from '../../components/Link';
import BackgroundImage from '../../components/BackgroundImage';
import Flex from '../../components/Flex';
import theme from '../../components/ThemeProvider/theme';
import FilterAndSearch from './FilterAndSearch';

const Catalogue = ({ shouldFix }) => {
  const { values, handleChange, setFieldValue } = useFormik({
    initialValues: {
      search: '',
    },
  })
  const data = useData()
  const filteredData = useMemo(() => {
    return data ? data.filter(d => d.img).map(d => {
      const isDisabled = Object.keys(values).reduce((res, key) => {
        if (!values[key]) return res
        if (key === 'search') return res || !new RegExp(values[key]).test(d.name)
        return res || (!d[key] || !d[key].includes(String(values[key])))
      }, false)
      return {
        ...d,
        isDisabled,
      }
    }) : []
  }, [data, values])
  return (
    <Box height="100%" bg="white">
      <Box position={shouldFix ? 'fixed' : 'absolute'} top={theme.headerHeight} left="0" right="0" bg="white" px="2em" zIndex="dropdown">
        <FilterAndSearch onChange={handleChange} setFieldValue={setFieldValue} values={values} />
      </Box>
      <Box pt={theme.headerHeight}>
        <Flex flexWrap="wrap" pt="1em">
          {filteredData.map(d => (
            <Box key={d.id} width={1 / 7} p="1em">
              <Link to={`/trash/${d.id}`} target="_blank">
                <Box border="0.125em solid black" rounded="1.5em" overflow="hidden">
                  <BackgroundImage ratio={1} src={d.img} />
                </Box>
              </Link>
            </Box>
          ))}
        </Flex>
      </Box>
    </Box>
  )
}

export default Catalogue
