import React, { useMemo } from 'react'
import {
  AccordionItem,
  AccordionButton,
  AccordionPanel,
  AspectRatio,
} from "@chakra-ui/react"

import Container from '../../../components/Container'
import Box from '../../../components/Box'
import Flex from '../../../components/Flex'
import Text from '../../../components/Text'
import Image from '../../../components/Image'
import ArrowDown from '../../../components/ArrowDown'
import BackgroundImage from '../../../components/BackgroundImage'
import { responsive } from '../../../components/ThemeProvider/theme'

import resultDetailShape from './result-detail-shape.svg'
import Trash from '../Cards/Trash'

const InTheBox = ({ title, items, opposite, countColor, wrongTag, ...props }) => {
  const count = useMemo(() => items.filter(d => !d.isCorrect).length, [items])
  return (
    <AccordionItem textAlign="center" {...props}>
      {({ isExpanded }) => (
        <Container py="4em">
          <Text fontSize={responsive('6em')} fontWeight="700">{title}</Text>
          <Box width={responsive('40em')} mx="auto">
            <BackgroundImage src={resultDetailShape} ratio={246.651 / 118.804}>
              <Text fontSize={responsive('2.5em')} py="2em">
                有
                <Text.Inline
                  verticalAlign="sub"
                  mx="1rem"
                  fontSize="2em"
                  fontFamily="number"
                  color={countColor}
                  textStroke="0.075em black"
                >{isExpanded ? count : '?'}</Text.Inline>
                個{opposite}
              </Text>
            </BackgroundImage>
          </Box>
          <AccordionPanel pt="2em" pb="4em">
            <Flex flexWrap="wrap">
              {items.map((item, i) => (
                <Box width="33.33%" key={i} p="2.5%">
                  <Box.Relative>
                    <AspectRatio ratio={1} border="0.25em solid black" rounded="4em" overflow="hidden">
                      <Trash layers={item.layers} />
                    </AspectRatio>
                    {!item.isCorrect && (
                      <Box.Absolute left="50%" right="-15%" top="50%">
                        <Image src={wrongTag} />
                      </Box.Absolute>
                    )}
                  </Box.Relative>
                </Box>
              ))}
            </Flex>
          </AccordionPanel>
          <AccordionButton display="inline-flex" width="auto" fontSize={responsive('3em')} border="0.125em solid black" rounded="full" px="1em">
            <Box mr="0.5em">
              {isExpanded ? '收合' : '打開瞧瞧'}
            </Box>
            <ArrowDown size="2rem" transform={`rotate(${isExpanded ? 0 : 180}deg)`} />
          </AccordionButton>
        </Container>
      )}
    </AccordionItem>
  )
}

export default InTheBox
