import React, { useMemo } from 'react'
import {
  Accordion,
} from "@chakra-ui/react"

import BackgroundImage from '../../../components/BackgroundImage'
import Box from '../../../components/Box'
import Text from '../../../components/Text'
import Image from '../../../components/Image'
import Button from '../../../components/Button'
import Container from '../../../components/Container'
import Flex from '../../../components/Flex'
import HighlightNumber from '../../../components/HighlightNumber'
import ArrowDown from '../../../components/ArrowDown'

import titleShape from './result-shape.svg'
import glow from './glow.svg'
import star from './star.svg'
import leftShape from './result-left-shape.svg'
import rightShape from './result-right-shape.svg'
import tierA from './tier-a.png'
import { responsive } from '../../../components/ThemeProvider/theme'
import InTheBox from './InTheBox'
import trashTag from './trash-tag.svg'
import recycleTag from './recycle-tag.svg'
import complete from './complete.svg'

const InArrowText = props => (
  <Text
    color="white"
    fontSize={responsive('3.5em')}
    textAlign="center"
    py="6.25rem"
    {...props}
  />
)

const ResultNumber = props => (
  <HighlightNumber
    mt="-1.5em"
    justifyContent="center"
    fontSize={responsive('4em')}
    numberProps={{ fontSize: '2.5em', color: 'colors.green' }}
    {...props}
  />
)

const Result = ({ answers, questions }) => {
  const [recycleBin, trashBin, correctCount] = useMemo(() => answers.reduce((bins, a, i) => {
    const q = questions[i]
    const isCorrect = q.recyclable && a.ans < 0
    bins[a.ans < 0 ? 0 : 1].push({
      ...q,
      isCorrect,
    })
    if (isCorrect) bins[2] += 1
    return bins
  }, [[], [], 0]), [answers, questions])
  return (
    <>
      <Container>
        <Box.Relative zIndex={1} textAlign="center">
          <BackgroundImage src={complete} ratio={147.118 / 80.116} mx="25%" >
            <Box fontSize={responsive('5em')} py="1.375em" color="colors.orange">
              任務完成
            </Box>
          </BackgroundImage>
        </Box.Relative>
        <Box.Relative mx="-8em">
          <Box.Absolute top="33%" left="-50%" right="-50%" transform="translateY(-50%)">
            <Image src={glow} />
          </Box.Absolute>
          <BackgroundImage src={titleShape} ratio={464.023 / 223.735}>
            <Box.Absolute left="18%" right="15%" top="15%">
              <Text fontSize={responsive('4em', '2em')}>你是...</Text>
              <Text fontSize={responsive('9em')} color="colors.pink" textStroke="0.02em black" fontWeight="900" letterSpacing="0.075em">垃圾管理大師</Text>
            </Box.Absolute>
          </BackgroundImage>
        </Box.Relative>
        <Box px="20%" mt="-20em">
          <BackgroundImage ratio={192 / 282} src={tierA} overflow="visible">
            <Box.Absolute left="-10%" top="50%" width="25%" transform="scale(-1,1)"><Image src={star} /></Box.Absolute>
            <Box.Absolute right="0" top="5%" width="25%"><Image src={star} /></Box.Absolute>
            <Box.Absolute right="3%" bottom="10%" width="30%"><Image src={star} /></Box.Absolute>
          </BackgroundImage>
        </Box>
        <Flex mt="-2em">
          <Box width="40%">
            <BackgroundImage src={leftShape} ratio={159.933 / 89.539}>
              <InArrowText>丟掉垃圾總量</InArrowText>
            </BackgroundImage>
            <ResultNumber number={answers.length}>個</ResultNumber>
          </Box>
          <Box width="20%" />
          <Box width="40%">
            <BackgroundImage src={rightShape} ratio={159.933 / 89.539}>
              <InArrowText>正確分類比率</InArrowText>
            </BackgroundImage>
            <ResultNumber number={Math.round(correctCount / answers.length * 100)}>％</ResultNumber>
          </Box>
        </Flex>
        <Box textAlign="center" mt="-3em">
          <Button.Yellow rounded="full" px="1em" fontSize={responsive('3em')}>分享結果</Button.Yellow>
          <Text my="1em" fontSize={responsive('2em')}>往下滑，看你丟對了哪些垃圾<ArrowDown ml="0.5em" size="1em" /></Text>
        </Box>
      </Container>
      <Accordion allowToggle>
        <InTheBox
          bg="colors.green"
          countColor="colors.yellow"
          title="資源回收桶裡..."
          opposite="一般垃圾"
          items={recycleBin}
          wrongTag={trashTag}
        />
        <InTheBox
          bg="colors.yellow"
          countColor="colors.green"
          title="一般垃圾桶裡..."
          opposite="資源回收"
          items={trashBin}
          wrongTag={recycleTag}
        />
      </Accordion>
      <Container py={responsive('6em')} textAlign="center">
        <Text fontSize={responsive('3.5em')}>想更深入認識每個垃圾嗎？</Text>
        <Button.Yellow to="/catalogue" my="1em" fontSize={responsive('3.5em')} rightIcon={<ArrowDown ml="0.25em" transform="rotate(90deg)" size="0.75em" />}>前往探索垃圾堆</Button.Yellow>
      </Container>
    </>
  )
}

export default Result
